// When using the script 'burger-menu' add class overflow
html {
  font-size: #{$browser-context}px;

  // @include media(">desktop") {
  //   font-size: #{$browser-context*0.7}px;
  // }

}

body {
  font-family: map-get($base, 'base-font');
  font-size: rem(18);
  line-height: 1.4;
  color: map-get($color, 'white');
  background-color: map-get($color, 'bg-color');
  overflow-x: hidden;

  &.noscroll {
    overflow-y: hidden;
  }

  &.overflow {
    overflow: hidden;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}

.container {
  max-width: rem(1180);
  width: 100%;
  padding: 0 rem(15);
  margin: auto;
}

.btn {
  border-radius: rem(33);
  font-weight: map-get($text, 'semi-bold');
  letter-spacing: 0.01em;
  text-transform: uppercase;
  font-size: rem(14);
  color: map-get($color, 'white');
  font-family: map-get($base, 'secondary-font');
  padding: rem(5) rem(40);
  min-height: rem(40);
  line-height: 1;

  &.text-left {
    text-align: left;
  }

  &.fz-20 {
    font-size: rem(20);
  }

  &.pl-60 {
    padding-left: rem(60);
  }

  p {
    font-size: rem(13);
    text-transform: initial;
    white-space: nowrap;
  }

  &.border {
    border: 1px solid map-get($color, 'grey-light');

    &:hover {
      color: map-get($color, 'grey-light');
    }
  }

  &.primary {
    background: map-get($color, 'primary');

    &:hover {
      background: map-get($color, 'hover-svg')
    }
  }

  &.big-btn {
    max-width: rem(220);
    width: 100%;
  }

  &.h-64 {
    min-height: rem(64);
  }

  &.icon {
    max-width: rem(253);
    width: 100%;
    min-height: rem(64);
    background-color: map-get($color, 'white');
    color: map-get($color, 'black-light');
    position: relative;

    svg {
      fill: map-get($color, 'primary');
      position: absolute;
      right: rem(12);
      top: 50%;
      transform: translateY(-50%);
      transition: all 0.3s ease;
    }

    &:disabled {
      color: map-get($color, 'grey-light');
      cursor: not-allowed;

      svg {
        fill: map-get($color, 'grey-light');
      }
    }

    &:hover {
      color: map-get($color, 'color-hover');

      svg {
        fill: map-get($color, 'hover-svg');
      }
    }
  }

  &.loading {
    max-width: rem(253);
    width: 100%;
    min-height: rem(64);
    background-color: map-get($color, 'white');

    span {
      background-color: map-get($color, 'hover-svg');
      opacity: 0.4;
      @include size(4px);
      display: inline-block;
      border-radius: 50%;
      margin: 2px;

      &:first-child {
        animation: animLoad1 2s ease 0s infinite forwards;
      }

      &:nth-child(2) {
        animation: animLoad2 2s ease 0s infinite forwards;
      }

      &:last-child {
        animation: animLoad3 2s ease 0s infinite forwards;
      }
    }
  }
}

@keyframes animLoad1 {
  30% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }

  40% {
    @include size(8px);
    opacity: 1;
    background-color: map-get($color, 'hover-svg');
  }

  50% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }

  100% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }
}

@keyframes animLoad2 {
  50% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }

  60% {
    @include size(8px);
    opacity: 1;
    background-color: map-get($color, 'hover-svg');
  }

  70% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }

  100% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }
}

@keyframes animLoad3 {
  70% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }

  80% {
    @include size(8px);
    opacity: 1;
    background-color: map-get($color, 'hover-svg');
  }

  90% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }

  100% {
    @include size(4px);
    opacity: 0.4;
    background-color: map-get($color, 'hover-svg');
  }
}

