.footer {
    padding-bottom: rem(65);
    padding-top: rem(60);

    @include media(">=tablet") {
        padding-top: rem(120);
        padding-bottom: rem(136);
    }

    .row {
        @extend %d-flex-align-start;
        flex-direction: column;

        @include media(">=tablet") {
            justify-content: space-between;
            align-items: stretch;
            flex-direction: row;
        }
    }

    &-logo {
        max-width: rem(141);
        width: 100%;
        margin-top: rem(20);

        @include media(">=tablet") {
            margin-top: 0;
        }
    }

    &-nav {
        ul {
            @extend %d-flex-column-start;

            li {
                margin-bottom: rem(14);

                @include media(">=tablet") {
                    &:last-child {
                        margin-bottom: 0;
                    }

                }

                a {
                    font-family: map-get($base, 'secondary-font');
                    color: map-get($color, 'white');
                    letter-spacing: -0.02em;
                    text-transform: uppercase;
                    font-size: rem(18);
                    line-height: 1.6;
                    font-weight: map-get($text, 'medium');

                    &:hover {
                        color: map-get($color, 'tifany');
                    }
                }
            }
        }
    }

    &-wiget {
        @extend %d-flex-column-start;
        justify-content: space-between;

        @include media(">=tablet") {
            align-items: flex-end;
        }

        a {
            margin-right: rem(30);

            &:last-child {
                margin-right: 0;
            }
        }
    }
}