.header {
  padding: rem(21) 0;
  position: relative;
  transition: all 0.3s ease;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2;

  @include media(">=tablet") {
    align-items: flex-start;
  }

  .row {
    max-width: rem(1400);
    width: 100%;
    margin: auto;
    padding: 0 rem(15);
  }

  &.active {
    background-color: map-get($color, "bg-secondary-color");
  }

  .row {
    @extend %d-flex-align-center;
    justify-content: space-between;
  }

  nav {
    // position: absolute;
    top: 100%;
    z-index: 11;
    width: 100%;
    // height: 0;
    right: 0;
    left: 0;
    background-color: map-get($color, "bg-secondary-color");
    transition: map-get($base, "base-transition");
    @extend %d-flex-column-center;
    justify-content: center;
    box-sizing: border-box;
    overflow-y: auto;

    @include media(">=tablet") {
      flex-direction: row;
    }

    &.active {
      height: 100vh;
      height: calc(100vh - 71px);

      @include media(">=tablet") {
        height: calc(100vh - 82px);
      }

      li {
        opacity: 0;
        transform: translateX(50%);
        animation: fadeMenu 0.5s 0.3s ease forwards;
      }

      @keyframes fadeMenu {
        0% {
          opacity: 0;
          transform: translateX(50%);
        }

        50% {
          opacity: 0;
        }

        100% {
          transform: translateX(0%);
          opacity: 1;
        }
      }
    }

    .nav-container {
      @extend %d-flex-column-start;
      max-width: rem(1146);
      width: 100%;
      margin: 0 auto;
      padding: 0 rem(15);
      overflow-x: hidden;
      overflow-y: hidden;
      box-sizing: border-box;

      @include media(">=tablet") {
        flex-direction: row;
        align-items: flex-end;
      }

      @include media(">=widescreen") {
        padding: 0;
      }
    }
  }


  .logo {
    max-width: rem(152);
    width: 100%;
    z-index: 6;
    line-height: 0;

    @include media(">=widescreen") {
      max-width: 190px;
    }

    a {
      width: 100%;
    }

    img {
      width: 100%;
      object-fit: contain;
    }
  }

  .pay {
    width: 100%;
    order: 2;
    margin-top: rem(20);

    @include media(">=tablet") {
      width: 50%;
      order: 1;
    }

    a {
      margin-right: rem(31);
    }
  }

  .nav-list {
    display: flex;
    flex-direction: column;
    font-size: rem(24);
    width: 100%;
    justify-content: space-between;
    order: 1;
    box-sizing: border-box;

    @include media(">=tablet") {
      width: 50%;
      order: 2;
    }

    @include media(">=desktop") {
      font-size: rem(48);
    }

    a {
      font-family: map-get($base, 'secondary-font');
      letter-spacing: -0.02em;
      color: map-get($color, 'white');
      text-transform: uppercase;
      font-weight: map-get($text, 'medium');
      line-height: 1.1;

      &:hover {
        color: map-get($color, 'tifany');
      }
    }
  }

  .burger {
    position: relative;
    z-index: 16;
    width: rem(29);
    height: rem(29);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    @include media(">=tablet") {
      transform: translateX(200%);
    }

    &:hover {
      span {
        background-color: map-get($color, 'white');

        &:after {
          background-color: map-get($color, 'white');
        }
      }
    }

    &.active {
      span {
        transition: 0.3s all;
        transform: rotate(45deg);
        margin-top: 2px;

        &:after {
          transition: 0.3s all;
          transform: rotate(-90deg);
          opacity: 1;
        }
      }
    }

    span {
      transition: all 0.3s ease;
      background-color: map-get($color, 'grey');
      display: block;
      position: relative;
      width: 100%;
      height: 2px;
      transition: 0.3s all;
      margin-top: -9px;

      &:after {
        background-color: map-get($color, 'grey');
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        transform: translateY(8px);
        border-radius: 10px;
        transition: 0.3s all;
      }
    }
  }

  .btn {
    margin: 0 !important;
    margin-right: rem(7) !important;

    &:last-child {
      margin-right: 0 !important;
    }
  }

  .mobile {
    flex-direction: row;
    margin-top: rem(20);
    margin-bottom: rem(20);
    order: 2;

    @include media(">=tablet") {
      display: none;
    }
  }

  .desktop {
    display: none;

    @include media(">=tablet") {
      display: flex;
    }
  }
}

.menu-container {
  max-width: rem(1400);
  width: 100%;
  margin: auto;
  padding: 0 1.5rem;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: absolute;
  top: 0%;
  left: 0;
  background: #0C1F29;
  pointer-events: none;
  opacity: 0;
  height: 100vh;
  display: none;
}

.menu-wrap.menu-wrap--open {
  pointer-events: auto;
  opacity: 1;
  display: flex;
}

.overlay {
  position: absolute;
  z-index: 1000;
  pointer-events: none;
  width: 100%;
  height: 100%;
}

.menu__item {
  position: relative;
  will-change: opacity, transform;
}

.button-close {
  position: absolute;
  top: rem(23);
  // right: rem(20);
  transform: translateX(50%);
  @include size(rem(30));
  cursor: pointer;
  

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: map-get($color, 'grey');
    transform: translateY(50%) rotate(45deg);
    transition: all 0.3s ease;
  }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: map-get($color, 'grey');
    transform: translateY(50%) rotate(-45deg);
    transition: all 0.3s ease;
  }
  &:hover{
    &::before,
    &::after {
      background-color: map-get($color, 'white');
    }
  }
}