// Global variables
//
// Base font styles for headings and links.
$browser-context: 10;

// For headers
$headings: ('sizes': ('h1-font-size': 96px,
    'h2-font-size': 48px,
    'h3-font-size': 40px,
    'h4-font-size': 21px,
    'h5-font-size': 17px,
    'h6-font-size': 15px),
);

// Global vars for body typography
$base: ('base-font': 'Krub',
  'secondary-font': 'Rajdhani',
  'base-transition': all 0.3s ease,
  'base-border-radius': 5px,
  'base-min-width': 320px);

//font-size vars
$text: ('extra-small-font': 10px,
  'small-font-size': 14px,
  'menu-font-size': 48px,
  'base-font-size': 16px,
  'semi-font-size': 18px,
  'big-font-size': 20px,
  'base-lh': 1.2,
  'secondary-lh': 1.4,
  'tertiary-lh': 1.6,
  'light': 300,
  'regular': 400,
  'medium': 500,
  'semi-bold': 600,
  'bold': 700,
  'extra-bold': 800,
  'black': 900,
);

//colors vars
$color: ('white': #FFFFFF,
  'black': #000000,
  'white-dark': #FBFBFB,
  'bg-color': #1A323E,
  'bg-secondary-color': #0C1F29,
  'primary': #3BBEBE,
  'blue-dark2': #0C1F29,
  'blue-dark3': #102834,
  'blue-dark4': #0E2633,
  'grey': #828282,
  'color-date': #9E9E9E,
  'color-border-figure': #D8D8D8,
  'grey-light': #979797,
  'grey-dark': #1A323E,
  'black-light': #161616,
  'blue-dark': #264251,
  'color-hover': #555555,
  'tifany': #50E3C2,
  'color-label': #4C6775,
  'bg-silver': #0D222D,
  'bg-gold': #0E2633,
  'bg-platinum': #102C3A,
  'bg-diamond': #163748,
  'hover-svg': #32AFAF,
  'error': #DC3545,
);

//box-shadow vars
$box-shadow: ('base-shadow': 0px 12px 24px rgba(35, 25, 86, 0.1));

// Vars for forms
$forms: ('form-element-border-color': #ffffff1a,
  'form-element-focus-border-color': #50E3C2,
  'placeholder-color': #fff);

// Breakpoints for include media mixin
$breakpoints: ('phone': 480px,
  'phone-lg': 576px,
  'tablet': 768px,
  'tablet-lg': 992px,
  'desktop': 1024px,
  'widescreen': 1200px,
  'widescreen-md': 1360px,
  'widescreen-lg': 1440px,
  'full-hd': 1920px,
);

// Keywords for include media mixin
$media-expressions: ('screen': 'screen',
  'print': 'print',
  'handheld': 'handheld',
  'landscape': '(orientation: landscape)',
  'portrait': '(orientation: portrait)',
  'retina2x': '(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi)',
  'retina3x': '(-webkit-min-device-pixel-ratio: 2.5), (min-resolution: 240dpi)'
);