.template {
    padding-top: rem(170);

    @include media(">=tablet") {
        padding-top: rem(192);
        margin-bottom: rem(88);
    }

    h1 {
        margin-bottom: rem(44);

        @include media(">=tablet") {
            margin-bottom: rem(88);
        }
    }
}

.privacy {
    .content {
        max-width: rem(1380);
        width: 95%;
        border-radius: rem(40);
        background-color: map-get($color, 'white');
        color: map-get($color, 'grey-dark');
        margin: auto;
        padding-top: rem(40);
        padding-bottom: rem(85);

        @include media(">=tablet") {
            padding-top: rem(80);
            padding-bottom: rem(177);
        }

        .h2 {
            margin-bottom: rem(16);
            margin-top: rem(30);
            line-height: 1.2;

            @include media(">=tablet") {
                margin-bottom: rem(32);
                margin-top: rem(60);
            }
        }

        p {
            font-size: rem(18);
            line-height: 1.4;
            letter-spacing: -0.02em;
            font-weight: map-get($text, 'medium');
            margin-bottom: rem(10);

            @include media(">=tablet") {
                margin-bottom: rem(20);
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &.article {
        .h1 {
            color: map-get($color, 'color-label');
        }

        .h2 {
            margin-top: 0;
            margin-bottom: rem(16);
        }

        a {
            transition: all 0.3s ease;

            svg {
                transition: all 0.3s ease;
            }

            &:hover {
                opacity: 0.8;

                svg {
                    transform: translateX(-20%);
                }
            }
        }

        figure {
            width: 100%;
            margin-top: rem(40);
            padding-bottom: rem(20);
            border-bottom: 1px solid map-get($color, 'color-border-figure');
            box-sizing: border-box;
            margin-bottom: rem(30);

            @include media(">=tablet") {
                margin-top: rem(40);
                padding-bottom: rtem(40);
                margin-bottom: rem(60);
            }
        }

        .content {
            padding-bottom: rem(60);

            @include media(">=tablet") {
                padding-bottom: rem(121);
            }
        }
    }
}

.date {
    font-size: rem(14);
    line-height: 1.4;
    letter-spacing: -0.02em;
    color: map-get($color, 'color-date')
}

.logo-list {
    @extend %d-flex-start-start;
    margin: rem(-11);

    .logo {
        width: calc(50% - 22px);
        height: rem(60);
        @extend %d-flex-align-center;
        justify-content: center;
        background-color: map-get($color, 'white-dark');
        border-radius: rem(20);
        margin: rem(11);
        padding: rem(6) rem(11);

        @include media(">=tablet") {
            width: calc(33% - 22px);
            padding: rem(11) rem(23);
        }

        @include media(">=desktop") {
            // width: rem(212);
            padding: rem(22) rem(46);
            width: calc(20% - 22px);
            height: rem(120);
        }

        img {
            width: 100%;
            height: 100%;
            border-radius: rem(20);
        }
    }
}

.news {
    a {
        transition: all 0.3s ease;

        &:hover {
            opacity: 0.8;
        }
    }

    &-head {
        @extend %d-flex-column;
        padding-bottom: rem(34);
        border-bottom: 1px solid map-get($color, 'color-border-figure');

        @include media(">=tablet") {
            flex-direction: row;
            justify-content: space-between;
            padding-bottom: rem(68);
        }

        .title {
            font-family: map-get($base, 'secondary-font');
            font-weight: map-get($text, 'semi-bold');
            font-size: rem(32);
            line-height: 1.2;
            text-transform: uppercase;
            margin-bottom: rem(16);
            color: map-get($color, 'grey-dark');
        }
    }

    &-main {
        max-width: rem(562);
        width: 100%;
        padding-bottom: rem(32);
        margin-bottom: rem(40);
        border-bottom: 1px solid map-get($color, 'color-border-figure');

        @include media(">=tablet") {
            padding-bottom: 0;
            margin-bottom: 0;
            border-bottom: none;
            padding-right: rem(15);
        }

        @include media(">=widescreen") {
            padding-right: 0;
        }

        .image {
            margin-bottom: rem(32);
        }
    }

    &-latest {
        max-width: rem(446);
        width: 100%;

        .item {
            padding-bottom: rem(32);
            margin-bottom: rem(40);
            border-bottom: 1px solid map-get($color, 'color-border-figure');

            &:last-child {
                padding-bottom: 0;
                margin-bottom: 0;
                border-bottom: none;
            }
        }
    }

    &-list {
        @extend %d-flex-start-start;
        flex-direction: column;
        padding-top: rem(34);

        @include media(">=tablet") {
            padding-top: rem(68);
        }

        .item {
            @extend %d-flex-column;
            align-items: stretch;
            justify-content: flex-start;
            margin-bottom: rem(32);

            @include media(">=tablet") {
                flex-direction: row;
                margin-bottom: rem(65);
            }

            &:last-child {
                margin-bottom: 0;
            }

            .img {
                flex: 1 0 329px;

                @include media(">=tablet") {
                    margin-right: rem(40);
                }
            }

            .title {
                font-family: map-get($base, 'secondary-font');
                font-weight: map-get($text, 'medium');
                font-size: rem(32);
                line-height: 1.2;
                text-transform: uppercase;
                color: map-get($color, 'grey-dark');
            }

            .info {
                @extend %d-flex-column;
                justify-content: space-between;
            }
        }
    }
}


.traders {
    .h2 {
        max-width: rem(563);
        width: 100%;
        position: relative;
        z-index: 1;
    }

    p {
        letter-spacing: -0.02em;
        margin-top: rem(32);
        margin-bottom: rem(30);
        max-width: rem(563);
        width: 100%;
        position: relative;
        z-index: 1;

        @include media(">=tablet") {
            margin-bottom: rem(60);
        }
    }

    .btn {
        position: relative;
        z-index: 1;
    }

    .download-block {
        position: relative;
        z-index: 1;
        padding: rem(20);
        border-radius: rem(12);
        background-color: map-get($color, 'blue-dark3');
        margin-top: rem(50);

        @include media(">=tablet") {
            margin-top: rem(281);
            padding: rem(40);
        }

        .title {
            font-weight: map-get($text, 'medium');
            text-transform: uppercase;
            margin-bottom: rem(51);
        }

        a {
            text-decoration: underline;
            color: map-get($color, 'white');

            svg {
                margin-left: rem(16);
            }
        }
    }
}

.template-content {
    position: relative;
    max-width: rem(1380);
    width: 90%;
    border-radius: rem(40);
    background-color: map-get($color, 'blue-dark2');
    color: map-get($color, 'white');
    margin: auto;
    padding-top: rem(40);
    padding-bottom: rem(40);

    @include media(">=tablet") {
        padding-top: rem(80);
        padding-bottom: rem(80);
    }

    .chart {
        position: relative;
        margin-top: rem(20);

        @include media(">=desktop") {
            position: absolute;
            margin-top: 0;
            left: 0;
            right: 0;
            top: 0;
            transform: translateY(40%);
        }
    }
}

.membership {
    .profits {
        margin-bottom: rem(60);
        position: relative;

        @include media(">=desktop") {
            padding-bottom: rem(450);
            margin-bottom: rem(120);
        }

        .h2 {
            margin-bottom: rem(16);
            position: relative;
            z-index: 1;

            @include media(">=desktop") {
                margin-bottom: rem(32);
            }
        }

        p {
            margin-bottom: rem(30);
            max-width: rem(563);
            position: relative;
            z-index: 1;

            @include media(">=desktop") {
                margin-bottom: rem(60);
            }
        }
        .btn{
            z-index: 1;
            max-width: rem(274);
            padding: rem(5) rem(24);
            svg{
                transform: translateY(-50%) rotate(135deg)
            }
        }
        .chart{
            line-height: 0;
            font-size: 0;
            width: 100%;
            bottom: 0;
            top: auto;
            left: 0;
            transform: translate(-7.5%, 10%)
        }
    }

    .calculator {
        @extend %d-flex-column-start;
        border-radius: rem(20);
        background-color: map-get($color, 'blue-dark4');
        padding: rem(20);
        margin-top: rem(62);

        @include media(">=tablet") {
            flex-direction: row;
            padding: rem(40);
            margin-top: rem(124);
        }

        .title {
            font-size: rem(18);
            line-height: 1.2;
            text-transform: uppercase;
            font-weight: map-get($text, 'medium');
        }

        .form,
        .total-block {
            width: 100%;

            @include media(">=tablet") {
                width: 50%;
            }
        }

        .form {
            @extend %d-flex-column-start;
            margin-bottom: rem(20);

            @include media(">=tablet") {
                margin-bottom: 0;
            }

            .title {
                margin-bottom: rem(24);

                @include media(">=tablet") {
                    margin-bottom: rem(48);
                }
            }

            .input {
                margin-bottom: rem(29);
                max-width: rem(253);
                width: 100%;

                @include media(">=tablet") {
                    margin-bottom: rem(58);
                }
            }
        }

        .total-block {
            .title {
                margin-bottom: rem(26);

                @include media(">=tablet") {
                    margin-bottom: rem(52);
                }
            }

            .profit {
                margin-bottom: rem(14);

                @include media(">=tablet") {
                    margin-bottom: rem(28);
                }

                &:last-child {
                    margin-bottom: 0;
                }

                p {
                    font-size: rem(24);
                    line-height: 1.2;
                    font-weight: map-get($text, 'medium');

                    &.green {
                        color: map-get($color, 'tifany');
                    }

                    @include media(">=tablet") {
                        font-size: rem(48);
                    }
                }

                span {
                    font-weight: map-get($text, 'medium');
                    text-transform: uppercase;
                    font-weight: map-get($text, 'medium');
                    font-size: rem(18);
                    line-height: 1.2;
                }
            }
        }
    }

    .tarifs {
        @extend %d-flex-row;
        align-items: flex-start;
        justify-content: flex-start;
        margin-top: rem(40);

        @include media(">=tablet") {
            margin-top: rem(80);
        }

        .slider {
            width: 50%;
            overflow: hidden;
            padding-bottom: rem(25);

            @include media(">=tablet") {
                width: calc(100% - 175px);

            }
        }

        .swiper-slide {
            @include media(">=widescreen-md") {
                max-width: rem(215);
                width: 100%;

            }
        }

        .item {
            border-radius: rem(12);
            position: relative;
            padding: rem(24) 0 rem(68);
            // max-width: rem(215);
            width: 100%;
            @extend %d-flex-column;
            align-items: center;
            justify-content: center;

            .title {
                text-transform: uppercase;
                font-size: rem(18);
                font-weight: map-get($text, 'medium');
                line-height: 1.2;
                text-align: center;
                margin-bottom: rem(41);
            }

            li {
                font-size: rem(18);
                text-align: center;
                margin-bottom: rem(32);
                height: rem(23);

                &:last-child {
                    margin-bottom: 0;
                }

                img {
                    width: rem(17);
                }
            }

            &.head {
                max-width: 50%;
                width: 100%;

                @include media(">=tablet") {
                    max-width: rem(175);
                    margin-right: rem(37);
                }

                li {
                    font-size: rem(14);
                    line-height: 1.2;
                    font-weight: map-get($text, 'regular');
                    text-align: left;
                }

            }

            &.silver {
                background-color: map-get($color, 'bg-silver');
            }

            &.gold {
                background-color: map-get($color, 'bg-gold');
            }

            &.platinum {
                background-color: map-get($color, 'bg-platinum');
            }

            &.diamond {
                background-color: map-get($color, 'bg-diamond');
            }

            .btn {
                position: absolute;
                bottom: 0;
                margin: auto;
                opacity: 0;
                cursor: pointer;
                transition: all 0.3s ease;
                transform: translateY(50%);
            }

            &:hover {
                .btn {
                    opacity: 1;
                }
            }
        }
    }

    .nav-slider {
        position: relative;
        height: rem(40);
        width: 100%;
        margin-top: rem(20);
    }

    .btn-prev,
    .btn-next {
        &::after {
            font-size: rem(22);
            color: map-get($color, 'tifany');
        }
    }

    .btn-prev {
        left: auto;
        right: 0;
        transform: translateX(-200%);
    }
}