.popups {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    // height: 100vh;
    z-index: 10;
    background-color: map-get($color, 'blue-dark2');
    overflow-y: auto;
    display: none;
    padding-bottom: rem(50);

    &.active {
        display: block;
    }

    .close {
        cursor: pointer;
    }

    .logo {
        max-width: rem(152);
        width: 100%;
    }

    &-header {
        @extend %d-flex-align-center;
        justify-content: space-between;
        max-width: rem(1380);
        width: 90%;
        margin: auto;
        padding: rem(32) 0;
    }
}

.popup {
    margin-top: rem(16);
    display: none;

    @include media(">=tablet") {
        margin-top: rem(32);
    }

    &.active {
        display: block;
    }

    &-title {
        letter-spacing: -0.02em;
        text-transform: uppercase;
        color: map-get($color, 'white');
        margin-bottom: rem(24);

        @include media(">=tablet") {
            margin-bottom: rem(48);
        }
    }

    .container {
        @extend %d-flex-row;

        &.justify-end {
            justify-content: flex-end
        }
    }

    &.apply-now {
        .form {
            width: 100%;
        }



        .form-row {
            span {
                font-size: rem(14);
                line-height: 2.1;
                color: map-get($color, 'color-label');
            }
        }

        .step {
            position: relative;
            font-size: rem(18);
            line-height: 1.2;
            text-transform: uppercase;
            color: map-get($color, 'white');
            font-weight: map-get($text, 'medium');
            max-width: rem(98);
            width: 100%;
            padding-bottom: rem(18);

            &::before {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                width: 100%;
                height: 1px;
                content: '';
                border-radius: rem(12);
                background-color: map-get($color, 'blue-dark');
            }

            &::after {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                height: 1px;
                content: '';
                border-radius: rem(12);
                background-color: map-get($color, 'tifany');
            }
        }

        .step-one {
            .step {
                &::after {
                    width: 50%;
                }
            }
        }

        .step-two {
            .step {
                &::after {
                    width: 100%;
                }
            }

            .left-block {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;

                @include media(">=tablet") {
                    padding-right: rem(63);
                }

            }
        }

        .step-one,
        .step-two {
            @extend %d-flex-column;

            @include media(">=tablet") {
                flex-direction: row;
            }
        }

        .prev-step {
            @extend %d-flex-row;
            align-items: center;
            font-size: rem(18);
            line-height: 1.2;
            color: map-get($color, 'color-label');
            text-transform: uppercase;
            font-weight: map-get($text, 'medium');
            cursor: pointer;

            svg {
                margin-right: rem(12);
            }
        }

        .left-block,
        .right-block {
            width: 100%;

            @include media(">=tablet") {
                width: 50%;
            }
        }

        .popup-step {
            display: none;

            &.active {
                display: flex;
            }
        }
    }
}

.form {
    width: 100%;

    @include media(">=tablet") {
        width: 50%;
    }

    &-row {
        @extend %d-flex-column-start;
        margin-bottom: rem(20);
        position: relative;
        max-width: rem(480);
        width: 100%;

        &:last-child {
            margin-bottom: 0;
        }

        @include media(">=tablet") {
            margin-bottom: rem(40);
        }

        .nice-select {
            .current {
                font-size: rem(18) !important;
                line-height: 1.6 !important;
                color: map-get($color, 'white') !important;
                font-weight: map-get($text, 'light') !important;
                padding: rem(9) 0;
            }

            padding: 0;
            background-color: transparent;
            max-width: rem(480);
            width: 100%;
            border: none;
            border-bottom: 1px solid map-get($forms, 'form-element-border-color');
            border-radius: 0;

            .option:hover,
            .option.focus,
            .option.selected.focus {
                background-color: map-get($color, 'blue-dark3');
            }

            .list {
                background-color: map-get($color, 'bg-secondary-color');
            }

            &::after {
                border: none;
                background-image: url(../images/select.svg);
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                width: rem(10);
                height: rem(7);
                transform: rotate(0);
            }

            &.open {
                &::after {
                    transform: rotate(-180deg);
                }
            }
        }
    }

    .label {
        position: absolute;
        top: rem(11);
        // transform: translateY(-50%);
        white-space: nowrap;
        overflow: hidden;
        width: 100%;
        transition: all 0.3s ease;

    }

    .input:invalid {
        border-color: map-get($color, 'error');
    }

    .input-js::placeholder {
        opacity: 0;
    }

    .input-js:not(:placeholder-shown)~.label-js,
    .input-js:focus~.label-js {
        top: 0;
        transform: translateY(-100%);
        font-size: rem(14);
        font-weight: map-get($text, 'light');
        line-height: 1.4;
        color: map-get($color, 'color-label');
    }

    p {
        font-size: rem(14);
        line-height: 1.4;
        color: map-get($color, 'color-label');
        margin-bottom: rem(30);

        @include media(">=tablet") {
            margin-bottom: rem(60);
        }

        a {
            color: map-get($color, 'tifany');
        }
    }

    .checkbox-row {
        max-width: rem(480);
        width: 100%;
        margin-bottom: rem(30);
        display: flex;

        @include media(">=tablet") {
            margin-bottom: rem(60);
        }

        .checkbox {
            width: 0;
            height: 0;
            overflow: hidden;
            opacity: 0;
        }

        label {
            padding-left: rem(56);
            font-size: rem(15);
            line-height: 1.3;
            position: relative;
            cursor: pointer;

            &::before {
                position: absolute;
                content: '';
                border-radius: 50%;
                left: 0;
                top: 0;
                @include size(rem(32));
                background-color: map-get($color, 'blue-dark');
            }
        }

        .checkbox:checked+label::before {
            background-color: map-get($color, 'hover-svg');
        }
    }
}