.main-section {
    padding-top: rem(124);
    padding-bottom: rem(68);
    // background: url(../images/bg-home.mp4);
    // background-repeat: no-repeat;
    // background-size: 100% 1075px;
    // background-position: top center;
    position: relative;

    @include media(">=tablet") {
        padding-top: rem(148);
        padding-bottom: rem(136);
    }

    .bg-video {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 73%;
        mix-blend-mode: luminosity;

        &::before {
            position: absolute;
            content: '';
            background-color: map-get($color, 'grey-dark');
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            mix-blend-mode: luminosity;
            opacity: 0.7;
        }

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .h1 {
        text-align: center;
        position: relative;
        z-index: 1;
    }

    .btn-container {
        margin-top: rem(20);
        position: relative;
        z-index: 1;
    }

    .chart-wrap {
        border-radius: rem(16);
        background-color: map-get($color, 'white');
        padding: rem(43) rem(20) rem(37);
        margin-top: rem(82);
        position: relative;
        z-index: 1;

        @include media(">=tablet") {
            padding: rem(43) 0 rem(37);
        }
    }

    .chart-title {
        font-size: rem(36);
        line-height: 1.2;
        color: map-get($color, 'grey-dark');
        margin-bottom: rem(30);
        font-weight: map-get($text, 'bold');
        text-transform: uppercase;
        opacity: 0.9;
    }

    .chart-container {
        max-width: rem(873);
        width: 100%;
        margin: auto;
    }

    .chart {
        width: 100%;

        img {
            width: 100%;
        }
    }
}

.platform {
    .row {
        @extend %d-flex-align-start;
        justify-content: flex-start;
        flex-direction: column;

        @include media(">=tablet") {
            flex-direction: row;
        }

        .img {
            max-width: rem(102);
            width: 100%;
            height: rem(148);
            background-color: map-get($color, 'blue-dark');
            border-radius: rem(20);
            margin-top: rem(20);
            margin-bottom: rem(20);

            @include media(">=tablet") {
                margin-top: rem(0);
                margin-bottom: rem(0);
                margin-right: rem(44);
            }
        }

        a {
            text-decoration: underline;
            color: inherit;
        }
    }

    &-grid {
        display: grid;
        grid-gap: rem(16);
        grid-template-columns: 1fr;
        grid-auto-rows: rem(320);
        grid-auto-flow: dense;
        margin-top: rem(50);
        width: 100%;

        @include media(">=tablet") {
            margin-top: rem(118);
            grid-template-columns: 1fr 1fr;
        }

        @include media(">=desktop") {
            grid-template-columns: 1fr 1fr 1fr;
        }

        .item {
            border-radius: rem(20);
            background-color: map-get($color, 'blue-dark');
            color: map-get($color, 'white');
            padding: rem(28) rem(28) rem(20);
            @extend %d-flex-column-start-between;

            @include media(">=tablet") {
                padding: rem(48) rem(48) rem(40);
            }

            .dots {
                position: relative;
                padding-left: rem(16);

                svg {
                    position: absolute;
                    left: 0;
                    top: 0;
                    transform: translateY(100%);
                }
            }

            &:hover {
                background-color: map-get($color, 'primary');
                cursor: pointer;
            }

            &.horizontal {
                grid-column: span 1;

                @include media(">=tablet") {
                    grid-column: span 1;
                }

                @include media(">=desktop") {
                    grid-column: span 2;
                }
            }

            &.vertical {
                grid-row: span 1;

                @include media(">=tablet") {
                    grid-row: span 1;
                }

                @include media(">=desktop") {
                    grid-row: span 2;
                }
            }

            &.small {
                grid-column: span 1;

                @include media(">=tablet") {
                    grid-column: span 1;
                }
            }

            transition: all 0.3s ease;

            &:hover {
                transform: scale(0.95);

                .h2,
                p {
                    letter-spacing: 0.05em;
                }
            }
        }

        .h2 {
            transition: all 0.3s ease;
        }

        p {
            letter-spacing: -0.02em;
            font-size: rem(18);
            line-height: 1.3;
            font-weight: map-get($text, 'medium');
            transition: all 0.3s ease;
        }
    }

    .join {
        margin-top: rem(22);
        height: rem(95);
        border-radius: rem(300);
        box-sizing: border-box;
        border: 2px solid map-get($color, 'white');
        width: 100%;
        font-size: rem(28);
        line-height: 1.2;
        font-weight: map-get($text, 'semi-bold');
        position: relative;
        display: flex;
        align-items: center;
        text-transform: inherit;

        @include media(">=tablet") {
            margin-top: rem(44);
        }

        svg {
            position: absolute;
            right: rem(5);
            fill: map-get($color, 'primary');
            transition: all 0.3s ease;
        }

        &:hover {
            color: map-get($color, 'grey-light');

            svg {
                fill: map-get($color, 'hover-svg');
            }
        }
    }
}

.partners {
    padding-top: rem(40);
    padding-bottom: rem(62);
    max-width: rem(1380);
    width: 95%;
    margin: auto;
    margin-top: rem(70);
    background-color: map-get($color, 'blue-dark');
    border-radius: rem(40);
    overflow: hidden;

    @include media(">=tablet") {
        padding-top: rem(80);
        padding-bottom: rem(123);
        margin: auto;
        margin-top: rem(140);
    }

    .h2 {
        margin-bottom: rem(25);

        @include media(">=tablet") {
            margin-bottom: rem(80);
        }
    }

    .logo-list {
        .row {
            @extend %d-flex-row;
            white-space: nowrap;
            flex-wrap: wrap;
        }

        .logo {
            margin: rem(11);
            width: calc(50% - 22px);
            height: rem(120);
            background: map-get($color, 'white');
            border-radius: rem(20);
            overflow: hidden;
            @extend %d-flex-center-center;
            box-sizing: border-box;

            @include media(">=tablet") {
                width: calc(25% - 22px);
            }

            @include media(">=desktop") {
                width: calc(20% - 22px);
            }

            img {
                width: 100%;
                object-fit: contain;
            }
        }
    }
}

.ocean-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    canvas {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
