.join-now {
    @extend %d-flex-align-center;
    max-width: rem(1380);
    border-radius: rem(20);
    width: 95%;
    margin: auto;
    margin-top: rem(120);
    min-height: rem(220);
    background-color: map-get($color, 'blue-dark');
    padding: rem(20) 0;

    @include media(">=tablet") {
        padding: 0;
    }

    .row {
        @extend %d-flex-align-center;
        justify-content: space-between;
        flex-direction: column;

        @include media(">=tablet") {
            flex-direction: row;
        }
    }
}

.btn-container {
    @extend %d-flex-align-center;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;

    @include media(">=tablet") {
        flex-direction: row;
    }

    .btn {
        margin-bottom: rem(20);

        @include media(">=tablet") {
            margin-right: rem(21);
            margin-bottom: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        &.big-btn {
            width: rem(220);
        }

        &.icon {
            width: rem(253);
        }
    }
}