.accordion {
    @extend %d-flex-column;
    align-items: flex-start;

    @include media(">=tablet") {
        flex-direction: row;
        flex-wrap: wrap;
    }

    &__item {
        width: 100%;
        border: 1px solid map-get($color, 'bg-diamond');
        border-radius: rem(20);
        margin-bottom: rem(20);
        overflow: hidden;

        @include media(">=tablet") {
            width: calc(50% - 20px);
            margin: 0 rem(10) rem(40);
        }
    }

    &__title {
        @extend %d-flex-justify-start;
        align-items: center;
        position: relative;
        cursor: pointer;
        font-family: map-get($base, 'secondary-font');
        font-weight: map-get($text, 'semi-bold');
        font-size: rem(22);
        line-height: 1.2;
        text-transform: uppercase;
        padding: rem(20);
        padding-right: rem(55);

        @include media(">=tablet") {
            font-size: rem(32);
            padding: rem(40);
            padding-right: rem(117);
        }

        &:after {
            position: absolute;
            content: '+';
            line-height: 1;
            right: rem(20);
            @include size(rem(36));
            @extend %d-flex-justify-center;
            align-items: center;
            border-radius: 50%;
            border: 1px solid map-get($color, 'bg-diamond');

            @include media(">=widescreen") {
                right: rem(40);
            }
        }

        &.active {
            background-color: map-get($color, 'bg-gold');

            &:after {
                content: '-';
                background-color: map-get($color, 'bg-diamond');
            }
        }
    }

    &__content {
        height: 0;
        overflow: hidden;
        transition: all 0.3s ease;
        opacity: 0;
        display: none;
        padding: 0 rem(55) rem(33) rem(20);

        @include media(">=tablet") {
            padding: 0 rem(117) rem(66) rem(40);
        }

        &.active {
            transition: 0.5s;
            background-color: map-get($color, 'bg-gold');
            height: auto;
            opacity: 1;
            display: block;
        }
    }
}